	font-size: 11em;
	font-weight: 700;
	margin-top: 0.53em;
	margin-bottom: 0.4em;
	text-transform: capitalize;
	font-family: 'Eczar';
	font-style: normal;
	color: #e44a66;
	line-height: 0.9;
	text-align: center;
	text-decoration: none;
}

h2 {
	font-size: 4.2em;
	font-weight: 900;
	margin-top: 0.4em;
	margin-bottom: 0.4em;
	font-family: 'Eczar';
}

h3 {
	font-size: 2.6em;
	font-weight: 800;
	margin-top: 1em;
	margin-bottom: 0.4em;
	font-family: 'Eczar';
	text-align: left;
}

h4 {
	font-size: 1.6em;
	font-weight: 800;
	margin-top: 2em;
