	font-family: 'Eczar';
}

h5 {
	font-size: 1.06em;
	font-weight: 700;
	margin-top: 3em;
	margin-bottom: 0.94em;
	font-family: 'Eczar';
}

h6 {
	font-size: 1em;
	font-weight: 700;
	margin-top: 2.6em;
